const { default: axios } = require('axios');
const MemoryCard = require('./MemoryCard');
const MemoryClock = require('./MemoryClock');
const MemoryHUD = require('./MemoryHUD');

// MemoryGameEngine est la class grant tout ce qui est relatif
//  la partie en cours.
class MemoryGameEngine {
  constructor(fruits, newGameCallback) {
    this.fruitsPerGame = 1;
		this.msBetweenFlip = 1000;
		this.maxSecondsPerGame = 120;
		this.fruits = fruits.shuffle().slice(0, this.fruitsPerGame);
    this.cards = [];
		this.HUD = new MemoryHUD();
    this.flippedCards = 0;
    this.canFlip = true;
    this.finishedAt = null;
    this.clock = new MemoryClock();
		// Puisque nous sommes dans une instance de `MemoryGameEngine`,
		// nous ne pouvons pas faire `this.newGame()` pour dmarrer
		// une nouvelle partie car cette mthode est propre  la class `Memory`.
		// Cependant on peut stocker cette mthode dans notre instance
		// pour pouvoir l'utiliser quand on le souhaite !
		this.newGameCallback = newGameCallback;
  }

	/**
	 * Rinitialise l'interface du jeu.
	 */
	clearHUD() {
		this.HUD.setElapsedTime(0, this.maxSecondsPerGame);
		this.HUD.setFlippedCards(0);
	}

	/**
	 * Actualise l'interface du jeu.
	 */
  refreshHUD() {
		this.HUD.setFlippedCards(this.flippedCards);
		this.HUD.setElapsedTime(this.clock.elapsedTime, this.maxSecondsPerGame);
  }

	/**
	 * Soumet les informations de la partie et du formulaire
	 *  notre serveur pour stocker les informations.
	 * @param username
	 */
	submitScore(username) {
		axios.post('/scoreSubmit', {
			username,
			elapsedTime: this.clock.elapsedTime,
			finishedAt: this.finishedAt,
		})
			.then(({ data }) => {
				// Si l'enregistrement des donnes a fonctionn,
				// on peut alors masquer supprimer le formulaire
				// de la fentre modale.
				this.HUD.updateLeaderboard(data.scores);
				this.HUD.hideResultsForm();
			});
	}

	/**
	 * Supprime la fentre modale et supprime les cartes de la grille de jeu
	 * pour laisser apparatre le tableau des scores.
	 */
	displayLeaderboardHandler() {
		// On fait disparatre doucement la fentre modale
		this.HUD.vanishResults();

		// Dans 500ms...
		setTimeout(() => {
			// ... on rend transparentes toutes les cartes...
			this.HUD.vanishBoard();

			// ... puis dans 300ms...
			setTimeout(() => {
				// ... on enlve le flou sur la grille de jeu...
				this.HUD.removeBlurBoard();
				// ... on supprime les cartes de la grille de jeu...
				this.HUD.removeCards();
				// ... on applique une classe CSS sur le tableau des scores
				// pour qu'il ait une opacity  0...
				this.HUD.vanishLeaderboard();
				// ... on enlve le `display: none;` sur le tableau des scores...
				this.HUD.displayLeaderboard();
			}, 300);
		}, 500);
	}

	/**
	 * Gestion des mthodes pour l'animation de disparition de la
	 * fentre modale de fin de partie et initialise une nouvelle partie.
	 */
	newGameHandler() {
		// On fait disparatre doucement la fentre modale
		this.HUD.vanishResults();

		// Dans 750ms...
		setTimeout(() => {
			// ... on force l'tat "cach" sur toutes les cartes de l'ancienne partie.
			this.HUD.closeAllCards();

			// ... puis dans 250ms...
			setTimeout(() => {
				// ... on enlve le flou sur la grille de jeu...
				this.HUD.removeBlurBoard();

				// ... et enfin (promis c'est le dernier !) aprs 300ms...
				setTimeout(() => {
					// ... on supprime du DOM la fentre modale...
					this.HUD.hideResults();
					// ... et on dmarre une nouvelle partie.
					this.newGameCallback();
				}, 300);
			}, 250);
		}, 750);

		// Ces trois `setTimeout` imbriqus peuvent heureusement tre dcomposs,
		// c'est--dire ne pas tre imbriqus  condition d'additionner les temps
		// d'attente de chaque `setTimeout` !
		//
		// Exemple :
		// 1. `setTimeout` imbriqus (250ms, puis 250ms)
		// 	setTimeout(() => {
		// 		/* ... */
		// 		setTimeout(() => {/* ... */}, 250);
		// 	}, 250);
		//
		// 2. `setTimeout` indpendants (250ms, puis 250ms + 250ms (500ms))
		// 	setTimeout(() => {/* ... */}, 250);
		// 	setTimeout(() => {/* ... */}, 500);
	}

	/**
	 * Gestion de la fin de partie.
	 */
	gameFinished() {
		// On stocke la date et heure de la fin de la partie.
		this.finishedAt = new Date();
		// On floute l'arrire plan.
		this.HUD.blurBoard();
    // On stoppe l'horloge interne du jeu.
    this.clock.end();
		// On bloque la possibilit de retourner de nouvelles cartes.
		this.canFlip = false;
		// On affiche l'cran de fin de jeu.
		this.HUD.displayResults(
			this.flippedCards,
			this.fruits.filter((fruit) => fruit.found).length,
			this.fruitsPerGame,
			this.clock.elapsedTime,
			() => this.newGameHandler(),
			(username) => this.submitScore(username),
			() => this.displayLeaderboardHandler()
		);
	}

	/**
	 * Actionne la fin de partie si toutes les paires ont t trouves.
	 */
	checkFoundPairs() {
		// Si le nombre de paires trouves (fruits trouvs)
		// est gal au nombre de fruits dans le jeu,
		// on enclenche la fin de la partie.
		if (this.fruits.filter((fruit) => fruit.found).length === this.fruitsPerGame) {
			this.gameFinished();
		}
	}
  
  /**
	 * Gestion des cartes actuellement retournes (hors cartes trouves).
   * @param {[]}
   */
	checkVisibleCards(cards) {
		// On modifie la valeur de la proprit "canFlip"  false pour
		// pouvoir interdire le retournement d'autres cartes.
		this.canFlip = false;

    // Si les deux cartes ont le mme fruit (apple, bananas etc)...
		if (cards[0].fruit.name === cards[1].fruit.name) {
			// ... alors on dfini ces cartes comme "trouves" et on enlve
			// l'tat de carte retourne.

			// Pour chacune de ces deux cartes...
			cards.forEach(card => {
				// ... on ajoute la classe CSS `Card--found`...
				card.DOMElement.classList.add('Card--found');
				// ... on indique la carte comme dj trouve...
				card.found = true;
				// ... et on en fait de mme pour le fruit.
				card.fruit.found = true;
			});

			// ... puis dans 300ms, on retire la classe CSS `Card--visible` puisque cette
			// carte fait partie du paire trouve.
			//
			// "300ms ? Mais pourquoi ?"
			// Nous avons une animation, lorsqu'une carte voit son tat de carte retourne altr, qui dure 300ms.
			// Si nous faisons les deux changements de classes CSS en mme temps,
			// l'animation ne pourra pas se faire correctement.
			// Il n'est question que de visuel, rien d'obligatoire, mais n'oublions pas que
			// l'UI reste importante pour les utilisateurs, du moment que l'UX est bonne !
			setTimeout(() => {
				cards.forEach((card) => card.DOMElement.classList.remove('Card--visible'));
			}, 300);

			// Et on autorise le fait de pouvoir retourner des cartes  nouveau.
			// Petit rappel : la ligne ci-dessous prend effet avant que la classe CSS soit supprime dans
			// le `setTimeout`, pas besoin d'attendre 300ms pour retourner de nouvelles cartes !
			this.canFlip = true;

			// On vrifie si toutes les paires ont t trouves pour ventuellement
			// dclencher la fin de la partie.
			this.checkFoundPairs();
		}
		else {
			// ... sinon, on retourne simplement les deux cartes
			// aprs un court dlai dfini sur `this.msBetweenFlip`.
			setTimeout(() => {
				cards.forEach((card) => {
					card.DOMElement.classList.remove('Card--visible');
					// On va galement autoriser de nouveau le retournement de cette
					// mme carte, puisque sa jumelle n'a pas t trouve.
					card.canFlip = true;
				});

				// On profite d'avoir retourn ces cartes pour galement autoriser
				// de nouveau le retournement des cartes, sans quoi le jeu se retrouverait bloqu.
				this.canFlip = true;
			}, this.msBetweenFlip);
		}
  }

	/**
	 * Vrifie si l'utilisateur est  court de temps pour dclencher la fin
	 * de la partie, ou bien laisser l'utilisateur continuer de jouer
	 * s'il lui reste du temps.
	 */
	checkElapsedTime() {
		if (this.clock.elapsedTime >= this.maxSecondsPerGame) this.gameFinished();
	}

	/**
	 * Incrmente le temps coul de 1  chaque appel
	 * puis rafraichi l'interface du jeu.
	 */
	incrementElapsedTime() {
		// On rajoute une seconde au timer...
		this.clock.elapsedTime += 1;
		// ... puis on actualise l'interface du jeu.
		this.refreshHUD();

		// Puisqu'il y a une limite de temps sur la partie,
		// on vrifie si le joueur ne l'a pas dpasse.
		this.checkElapsedTime();
	}
  
  /**
	 * Gestion du retournement des cartes vers face visible.
	 * @param {MemoryCard}
	 */
	flipCard(currentCard) {
    // Si le joueur ne peut pas retourner de cartes pour le moment,
    // on sort de la mthode `flipCard`.
    if (!this.canFlip) return;

    // Si l'horloge de jeu n'a pas encore dmarr,
    // on la dmarre de ce pas !
    // Elle n'est pas active avant de cliquer pour
    // la premire fois sur une carte.
    if (null === this.clock.id) this.clock.start(() => this.incrementElapsedTime());

		// On indique la carte clique comme retourne avec une classe CSS (face visible)...
		currentCard.DOMElement.classList.add('Card--visible');
		// ... on donne  notre objet le status retourn...
		currentCard.flipped = true;
    // ... on incrmente le nombre total de cartes retournes...
    this.flippedCards += 1;
    // ... puis on rafraichi l'HUD !
    this.refreshHUD();

		// On rcupre toutes les cartes faces visibles (hors cartes trouves).
		// Puisqu'on bloque le nombre de cartes retournes  deux lors de la
		// vrification des cartes (this.checkVisibleCards()),
		// `visibleCards` aura soit une carte, soit deux cartes, mais jamais plus !
    const visibleCards = this.cards.filter((card) => card.flipped);

    // Si exactement deux cartes sont actuellement retournes...
		if (2 === visibleCards.length) {
			// ... alors on vrifie qu'elles sont identiques...
			this.checkVisibleCards(visibleCards);
			// ... puis on retire le status "retourn" sur ces deux cartes.
			visibleCards.forEach((card) => card.flipped = false);
		}
	}

	/**
	 * Supprime les cartes du DOM et du systme de l'application.
	 */
	removeCards() {
		// On supprime les cartes de la grille de jeu...
		this.HUD.removeCards();
		// ... puis on vide notre proprit `this.cards`.
		this.cards = [];
	}

	/**
	 * Gestion de la cration des cartes pour la nouvelle grille de jeu.
	 */
	createCards() {
		// Ici, on cre un tableau avec deux fois les valeurs contenues
		// dans la proprit `this.fruits`.
		// Cela nous permet d'avoir deux fois chaque fruit !
		// Par contre, ils ne sont pas mlangs entre eux, on va donc
		// utiliser notre prototype sur les arrays pour mlanger
		// ce joli tableau pour viter que les cartes soient toujours
		// aux mmes positions sur la grille de jeu.
		[...this.fruits, ...this.fruits].shuffle().forEach(fruit => {
      // Ici, on gre l'instanciation d'une nouvelle carte pour
      // le fruit qui lui a t donn et on ajoute un eventListener...
			const newCard = new MemoryCard(fruit, (card) => this.flipCard(card));

      // ... puis on le configure pour qu'il corresponde au fruit choisi
      // tout en prenant soin de l'insrer dans la grille de DOM...
			newCard.prepareCardDOM(this.HUD.cardTemplate, this.HUD.boardContainer);

      // ... et ici, on stocke notre objet dans la liste
      // des cartes de la partie en cours.
			this.cards.push(newCard);
		});
	}
}

module.exports = MemoryGameEngine;
