// Bas sur l'algorithme de Fisher-Yates (mlange de Knuth)
// => https://fr.wikipedia.org/wiki/M%C3%A9lange_de_Fisher-Yates

/**
 * Cre un nouveau tableau mlang,  partir du tableau sur lequel cette
 * mthode est utilise.
 * @returns {[]}
 */
 Array.prototype.shuffle = function() {
	// On cre un nouveau tableau  partir du tableau dj existant
	let array = Array.from(this);

	// On dfini l'index actuel sur la taille du nouveau tableau
	let currentIndex = array.length;

	// On cre un index qui recevra un index au hasard  chaque passage dans la boucle
	let randomIndex;

	// Tant qu'il reste des lments  mlanger... (= position de l'index diffrent de 0)
	while (currentIndex !== 0) {
		// ... on prend au hasard un lment restant...
		randomIndex = Math.floor(Math.random() * currentIndex);

		// ... on dcrmente la position de notre index...
		currentIndex--;

		// ... puis on fait une inversion sur le contenu de l'index pris au hasard avec celui de notre index qui dcrmente
		//  chaque passage dans la boucle
		[array[currentIndex], array[randomIndex]] = [array[randomIndex], array[currentIndex]];
	}

	// Une fois que le mlange est termin, on retourne le tableau.
	return array;
}